___CSS_LOADER_EXPORT___.push([module.id, ".dropzone[data-v-37723574]{align-items:center;border:2px dashed #6b7280;display:flex;flex-direction:column;justify-content:center;row-gap:16px;transition:all .3s ease}label[data-v-37723574]{padding:8px 12px;transition:all .3s ease}.active-dropzone[data-v-37723574],label[data-v-37723574]{background-color:#4bb1b1;color:#fff}.active-dropzone[data-v-37723574]{border-color:#fff}.active-dropzone label[data-v-37723574]{background-color:#fff;color:#4bb1b1}", "",{"version":3,"sources":["webpack://./resources/js/Components/Uploaders/VideoUpload.vue"],"names":[],"mappings":"AAoFA,2BAII,kBAAmB,CAEnB,yBAA0B,CAL1B,YAAa,CACb,qBAAsB,CACtB,sBAAuB,CAEvB,YAAa,CAEb,uBACJ,CAEA,uBACI,gBAAiB,CAGjB,uBACJ,CAEA,yDAJI,wBAAyB,CADzB,UASJ,CAJA,kCAEI,iBAEJ,CAEA,wCACI,qBAAsB,CACtB,aACJ","sourcesContent":["<template>\n    <div>\n\n        <progress v-show=\"userStore.uploadPercentage != 0\" max=\"100\" :value=\"userStore.uploadPercentage\" class=\"w-full mb-4\" />\n\n\n        <form id=\"videoUploadForm\" action=\"/videoupload\" class=\"dropzone dropzoneFile border border-gray-400 rounded w-full h-48 max-w-md px-2 py-2 mb-6\">\n            <!--                            add input fields and a submit button to send data back to Laravel -->\n\n\n        </form>\n\n    </div>\n</template>\n\n<script setup>\nimport { Dropzone } from \"dropzone\";\nimport { useForm } from \"@inertiajs/inertia-vue3\"\nimport {useUserStore} from \"@/Stores/UserStore\";\nimport {onMounted, ref} from \"vue\";\nimport {Inertia} from \"@inertiajs/inertia\";\n\nlet userStore = useUserStore()\nlet uploadPercentage = ref(0);\n\nonMounted(() => {\n// see options for Dropzone here: https://github.com/dropzone/dropzone/blob/main/src/options.js\nlet myDropzone = new Dropzone(\"#videoUploadForm\", {\n    url: \"/videoupload\",\n    paramName: \"file\", // The name that will be used to transfer the file\n    maxFilesize: '50 GB', // MB\n    chunking: true,\n    chunkSize: 2 * 1024 * 1024,\n    parallelChunkUploads: false,\n    retryChunks: false,\n    retryChunksLimit: 10,\n    capture: 'camcorder',\n    acceptedFiles: 'video/*, audio/*',\n    uploadprogress: function(file, progress, bytesSent) {\n        userStore.uploadPercentage = progress;\n        console.log(userStore.uploadPercentage);\n    },\n    dictDefaultMessage: \"Click here or Drop files here to upload\",\n    forceFallback: false, // for testing, set to true.\n    accept: function(file, done) {\n        if (file.name == \"\") {\n            done(\"Need a file.\");\n        }\n        else { done(); }\n    }\n});\n\nmyDropzone.on(\"addedfile\", file => {\n    console.log(`File added: ${file.name}`);\n\n});\n\nmyDropzone.on(\"complete\", function(file) {\n    myDropzone.removeFile(file);\n    userStore.uploadPercentage = 0;\n    Inertia.reload({\n        only: [\"videos\"],\n    });\n});\n\n})\n\nlet form = useForm({\n    file: [],\n});\n\n// let props = defineProps({\n//     filters: Object,\n//     can: Object,\n//     videos: Object,\n//     message: String,\n//     errors: ref(''),\n//     isHidden: ref(false),\n//     done: ref(),\n// });\n\n</script>\n<style scoped>\n\n.dropzone {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    row-gap: 16px;\n    border: 2px dashed #6b7280;\n    transition: 0.3s ease all;\n}\n\nlabel {\n    padding: 8px 12px;\n    color: #fff;\n    background-color: #4bb1b1;\n    transition: 0.3s ease all;\n}\n\n.active-dropzone {\n    color: #fff;\n    border-color: #fff;\n    background-color: #4bb1b1;\n}\n\n.active-dropzone  label {\n    background-color: #fff;\n    color: #4bb1b1;\n}\n\n\n</style>\n"],"sourceRoot":""}]);
